import { Link } from "react-router-dom";
import { Preloader, ErrorMessage, Breadcrumb } from "../../components";
import useFetch from "../../hooks/useFetch.hook";
import { useSelector } from "react-redux";


const Categories = () => {
  const { categories } = useSelector(state => state.categoriesManager)
  const { isLoading, errorMessage } = useFetch("category.json");

  if (isLoading) return <Preloader />;
  if (errorMessage) return <ErrorMessage errorMessage={errorMessage} />

  return (
    <div className="body-wrapper space-pb--120">
      <Breadcrumb pageTitle="Categories" />
      <div className="category-area">
        <div className="container">
          <div className="row category-wrapper">
            {categories.map((category) => {
              return (
                <div className="col-12" key={category._id}>
                  <Link
                    to={"/contacts/" + category.cat}
                    className="single-category bg-img"
                    style={{
                      backgroundImage: `url(${process.env.PUBLIC_URL + category.img
                        })`
                    }}
                  >
                    {/* <span className="single-category__image">
                      <img
                        src={process.env.PUBLIC_URL + category.img}
                        className="injectable"
                        alt=""
                      />
                    </span> */}
                    <span className="single-category__title">
                      {category.title}
                    </span>
                  </Link>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Categories;
